<template>
  <section class="list">
    <h4>Borrow Markets</h4>
    <el-table
      v-if="borrowList.length > 0"
      class="table-box"
      ref="singleTable"
      :data="borrowList"
      style="width: 100%"
      @row-click="handleSeleUserId"
      :header-cell-style="{ color: '#AAB8C1' }"
      header-row-class-name="tableheader"
    >
      <el-table-column label="Asset">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="APY / Accrued">
        <template slot-scope="scope">
          {{ scope.row.borrowAPY | toPercent(100) }}{{ "%" }}
          <br />
          {{ "TBD" }}
        </template>
      </el-table-column>
      <el-table-column label="Balance">
        <template slot-scope="scope">
          {{ "$"
          }}{{
            scope.row.balance.underlyingValue
              | fromToken(USDT_DECIMAL_PLACES, 2)
          }}
          <br />
          {{
            scope.row.balance.borrowBalanceCurrent
              | fromToken(scope.row.underlyingDecimals, 2)
          }}{{ " " + scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="% Of Limit">
        <template slot-scope="scope">
          <el-progress
            :percentage="$toPercent(scope.row.balance.limitPercent, 100)"
          ></el-progress>
        </template>
      </el-table-column>
    </el-table>
    <span
      class="panel-label row"
      @click="showAllList = !showAllList"
      v-if="borrowList.length > 0 && allList.length > 0"
    >
      <label>All Markets</label>
      <span class="dropdown-icon" :class="{ active: showAllList }"></span>
    </span>
    <el-table
      v-if="allList.length > 0 && showAllList"
      class="table-box"
      ref="singleTable"
      :data="allList"
      style="width: 100%"
      @row-click="handleSeleUserId"
      :header-cell-style="{ color: '#AAB8C1' }"
      header-row-class-name="tableheader"
    >
      <el-table-column label="Asset">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="APY">
        <template slot-scope="scope">
          {{ scope.row.borrowAPY | toPercent(100) }}{{ "%" }}
        </template>
      </el-table-column>
      <el-table-column label="Wallet">
        <template slot-scope="scope">
          {{
            scope.row.balance
              ? scope.row.balance.tokenBalance
              : 0 | fromToken(scope.row.underlyingDecimals, 2)
          }}{{ " " + scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="Liquidity">
        <template slot-scope="scope">
          ${{
            scope.row.totalCashValue
              | fromToken(USDT_DECIMAL_PLACES, 2)
          }}
          <br />
          {{ scope.row.totalCash | fromToken(scope.row.underlyingDecimals, 2) }}
          {{ " " + scope.row.name }}
        </template>
      </el-table-column>
    </el-table>

    <Dialog
      v-if="dialogVisible"
      @closeDialogVisible="closeDialogVisible"
      :currentForm="currentForm"
    />
  </section>
</template>

<script>
import Dialog from "./DashboardBorrowDialog";
import { mapGetters } from "vuex";
import { USDT_DECIMAL_PLACES } from "@/config/constants";

export default {
  name: "DashboardBorrow",
  components: {
    Dialog,
  },
  data() {
    return {
      USDT_DECIMAL_PLACES: USDT_DECIMAL_PLACES,
      currentCtoken: null,
      x_currentForm: {
        cToken: "",
        asset: null,
        apy: null,
        wallet: null,
        danwei: null,
        collateral: null,
        liquidity: 0,
        supplyAPY: 0,
        supplyRatePerBlock: 0,
        borrowAPY: 0,
        borrowRatePerBlock: 0,
        underlyingDecimals: 0,
        balance: {
          tokenBalance: 0,
        },
      },
      dialogVisible: false,
      input: "888",
      showAllList: true,
    };
  },
  created() {},
  mounted() {},
  computed: {
    ...mapGetters(["markets", "address", "accountInited"]),
    allList: function () {
      return this.accountInited
        ? this.markets.filter(
            (item) =>
              item.balance.borrowBalanceCurrent == 0 &&
              item.balance.balanceOfUnderlying == 0
          )
        : this.markets;
    },
    borrowList: function () {
      return this.accountInited
        ? this.markets.filter((item) => item.balance.borrowBalanceCurrent > 0)
        : [];
    },
    currentForm() {
      return this.markets.find((market) => market.cToken == this.currentCtoken);
    },
  },
  methods: {
    async handleSeleUserId(row) {
      //g :row, column, event
      this.currentCtoken = row.cToken;
      if(!this.accountInited)
        await this.$store.dispatch('login', true);
      this.dialogVisible = true;
    },
    closeDialogVisible() {
      this.dialogVisible = false;
    },
  },
};
</script>

