<template>
  <section>
    <!-- <h4>Supply Markets</h4> -->
    <el-dialog
      :title="currentForm.cToken"
      visible
      :close-on-click-modal="false"
      :width="screenWidth > defaultWidth ? defaultWidth + 'px' : '96%'"
      center
      @close="closeDialog"
    >
      <div slot="title" class="dialog-title">
        <!-- <i class="el-icon-lollipop"></i> -->
        <span class="title-text">{{ currentForm.name }}</span>
      </div>
      <el-tabs type="border-card">
        <el-tab-pane label="Supply">
          <div class="formmain">
            <div
              v-if="currentForm.balance.tokenAllowance > 0"
              class="input calculation"
            >
              <el-input
                v-model="supplyAmount"
                placeholder="0"
                :controls="false"
                @input="onChangeInp"
              ></el-input>
              <el-button
                class="max-btn"
                type="info"
                size="mini"
                @click="supplyMaxfn"
              >
                MAX
              </el-button>
            </div>
            <div v-else>
              <img v-if="false" class="asset-logo" src="../../assets/logo.png" alt="" />
              <p class="small text-center">
                To Supply or Repay {{ currentForm.name }} to the Coco Lending, you need to enable it first.
              </p>
            </div>
            <div class="info">
              <div class="form">
                <!-- <a class="label-link"> -->
                <label class="dark label-link">Supply Rates</label>
                <!-- </a> -->
                <div class="calculation bottom-line">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Supply APY</span>
                  </span>
                  <span>
                    {{ currentForm.supplyAPY | toPercent(100) }}{{ "%" }}
                  </span>
                </div>
                <div class="calculation">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Distribution APY</span>
                  </span>
                  <span>
                    <!-- APY210602 -->
                    {{ "-- " }}
                  </span>
                </div>
              </div>
              <div class="form">
                <label class="dark">Borrow Limit</label>
                <div class="calculation bottom-line">
                  <span class="description">Borrow Limit</span>
                  <div class="row">
                    <span>
                      ${{ totalLimit | fromToken(USDT_DECIMAL_PLACES) }}
                    </span>
                    <span
                      v-if="
                        $compare(
                          $toToken(
                            supplyAmount,
                            currentForm.underlyingDecimals
                          ),
                          0
                        ) > 0
                      "
                    >
                      {{ " -> " }}${{
                        hopeTotalLimit | fromToken(USDT_DECIMAL_PLACES)
                      }}
                    </span>
                  </div>
                </div>
                <div class="calculation">
                  <span class="description">Borrow Limit Used</span>
                  <div class="row">
                    <span
                      >{{ borrowedLimitPercent | toPercent(100)
                      }}{{ "%" }}</span
                    >
                    <span v-if="supplyAmount > 0">
                      {{ " -> " }}{{ supplyAmountPercent | toPercent(1)
                      }}{{ "%" }}
                    </span>
                  </div>
                </div>
                <el-progress
                  :percentage="supplyAmountPercent"
                  :show-text="false"
                ></el-progress>
              </div>
              <div class="action-button">
                <el-button
                  v-if="currentForm.balance.tokenAllowance > 0"
                  type="danger"
                  style="width: 100%"
                  @click="supply"
                  :loading="loading"
                  :disabled="
                    $compare(
                      $toToken(supplyAmount, currentForm.underlyingDecimals),
                    $toToken(supplyAmount, currentForm.underlyingDecimals), 
                      $toToken(supplyAmount, currentForm.underlyingDecimals),
                      currentForm.balance.tokenBalance
                    ) > 0
                  "
                >
                  SUPPLY
                </el-button>
                <el-button
                  v-else
                  type="primary"
                  style="width: 100%"
                  @click="enable"
                  :loading="loading"
                >
                  ENABLE
                </el-button>
              </div>
              <div class="bottom-note">
                <div class="calculation">
                  <span class="description">Wallet Balance</span>
                  <span>
                    {{
                      currentForm.balance.tokenBalance
                        | fromToken(currentForm.underlyingDecimals, 2)
                    }}
                    {{ currentForm.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="Redeem">
          <div class="formmain">
            <div class="input calculation">
              <el-input
                v-model="redeemAmount"
                placeholder="0"
                :controls="false"
                @input="onChangeInpR"
              ></el-input>
              <el-button
                class="max-btn"
                type="info"
                size="mini"
                @click="redeemMaxfn"
              >
                SAFE MAX
              </el-button>
            </div>
            <div class="info">
              <div class="form">
                <!-- <a class="label-link"> -->
                <label class="dark label-link">Supply Rates</label>
                <!-- </a> -->
                <div class="calculation bottom-line">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Supply APY</span>
                  </span>
                  <span>
                    {{ currentForm.supplyAPY | toPercent(100) }}{{ "%" }}
                  </span>
                </div>
                <div class="calculation">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Distribution APY</span>
                  </span>
                  <span>
                    <!-- APY210602 -->
                    {{ "-- " }}
                  </span>
                </div>
              </div>
              <div class="form">
                <label class="dark">Borrow Limit</label>
                <div class="calculation bottom-line">
                  <span class="description">Borrow Limit</span>
                  <div class="row">
                    <span>
                      ${{ totalLimit | fromToken(USDT_DECIMAL_PLACES) }}
                    </span>
                    <span v-if="redeemAmount > 0">
                      {{ " -> " }}${{
                        $fromToken(hopeTotalLimit, USDT_DECIMAL_PLACES)
                      }}
                    </span>
                  </div>
                </div>
                <div class="calculation">
                  <span class="description">Borrow Limit Used</span>
                  <div class="row">
                    <span>
                      {{ borrowedLimitPercent | toPercent(100) }}{{ "%" }}
                    </span>
                    <span v-if="redeemAmount > 0">
                      {{ " -> " }}{{ redeemAmountPercent | toPercent(1)
                      }}{{ "%" }}
                    </span>
                  </div>
                </div>
              </div>
              <el-progress
                :percentage="redeemAmountPercent"
                :show-text="false"
              ></el-progress>
              <div class="action-button">
                <el-button
                  v-if="
                    $compare(
                      $toToken(redeemAmount, currentForm.underlyingDecimals),
                      currentForm.balance.balanceOfUnderlying
                    ) <= 0
                  "
                  type="primary"
                  style="width: 100%"
                  @click="redeem"
                  :loading="loading"
                >
                  REDEEM
                </el-button>
                <el-button v-else type="info" style="width: 100%" disabled>
                  INSUFFICIENT LIQUIDITY
                </el-button>
              </div>
              <div class="bottom-note">
                <div class="calculation">
                  <span class="description">Currently Supplying</span>
                  <span>
                    {{
                      currentForm.balance.balanceOfUnderlying
                        | fromToken(currentForm.underlyingDecimals, 2)
                    }}
                    {{ currentForm.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </section>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { USDT_DECIMAL_PLACES } from "@/config/constants";

export default {
  name: "DashboardSupplyDialog",
  props: {
    currentForm: Object,
  },
  data() {
    return {
      USDT_DECIMAL_PLACES: USDT_DECIMAL_PLACES,
      screenWidth: document.body.clientWidth,
      defaultWidth: 400,
      redeemAmount: 0,
      supplyAmount: 0,
      status: {
        enabled: false,
        ctokenBalance: 0,
        borrowBalance: 0,
        marketRate: 0,
      },
      cToken: null,
      hopeBorrowed: 0,
      hopeTotalLimit: 0,
      hopeLimitPercent: 0,
      loading: false,
    };
  },
  computed: {
    ...mapGetters([
      "address",
      "contracts",
      "fullscreenLoading",
      "borrowBalance",
      "totalLimit",
      "borrowedLimitPercent",
      "globalInfo",
      "markets",
    ]),
    supplyAmountPercent: function () {
      let percent = 0;
      if (this.supplyAmount > 0) {
        percent = this.$toPercent(this.hopeLimitPercent, 100) * 1;
      } else {
        percent = this.$toPercent(this.borrowedLimitPercent, 100);
      }
      if (percent < 0) {
        percent = 0;
      }
      if (percent > 100) {
        percent = 100;
      }
      return percent;
    },
    redeemAmountPercent: function () {
      let percent = 0;
      if (this.redeemAmount > 0) {
        percent = this.$toPercent(this.hopeLimitPercent, 100) * 1;
      } else {
        percent = this.$toPercent(this.borrowedLimitPercent, 100);
      }
      if (percent < 0) {
        percent = 0;
      }
      if (percent > 100) {
        percent = 100;
      }
      return percent;
    },
  },
  created() {},
  mounted() {
    console.log("this.currentForm --- ", this.currentForm);
    this.cToken = this.contracts.find(
      (item) => item.cToken.cToken.address == this.currentForm.cToken
    ).cToken;
  },
  methods: {
    ...mapMutations(["setShowFullscreenLoading"]),
    async enable() {
      this.loading = true;
      const tx = await this.cToken.enableMarket();
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async supply() {
      if (this.supplyAmount === "" || this.supplyAmount == 0) {
        this.$notify.error({
          title: "error",
          message: "empty",
        });
        return false;
      }
      let maxSupplyAmount = this.currentForm.balance.tokenBalance;
      const stakeAmount = this.$toToken(
        this.supplyAmount,
        this.currentForm.cTokenDecimals
      );
      if (this.$compare(stakeAmount, maxSupplyAmount) > 0) {
        this.$notify.error({
          title: "error",
          message: "insufficient funds",
        });
        return false;
      }
      this.loading = true;
      const tx = await this.cToken.mint(stakeAmount);
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async redeem() {
      if (this.redeemAmount === "" || this.redeemAmount == 0) {
        this.$notify.error({
          title: "error",
          message: "empty",
        });
        return false;
      }
      let maxRedeemAmount = this.currentForm.balance.balanceOfUnderlying;
      const stakeAmount = this.$toToken(
        this.redeemAmount,
        this.currentForm.cTokenDecimals
      );
      if (this.$compare(stakeAmount, maxRedeemAmount) > 0) {
        this.$notify.error({
          title: "error",
          message: "insufficient funds",
        });
        return false;
      }
      this.loading = true;
      const tx = await this.cToken.redeem(stakeAmount);
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async supplyMaxfn() {
      const info = this.globalInfo;
      const maxSupplyAmount = await info.perentLimit(
        this.currentForm.cToken,
        "mint"
      );
      this.supplyAmount = this.$formatUnits(
        maxSupplyAmount,
        this.currentForm.underlyingDecimals
      );
      this.onChangeInp();
    },
    async redeemMaxfn() {
      const info = this.globalInfo;
      const maxRedeemAmount = await info.perentLimit(
        this.currentForm.cToken,
        "redeem",
        this.currentForm.isCollateral && this.borrowBalance > 0 ? 0.8 : 1
      );
      this.redeemAmount = this.$formatUnits(
        maxRedeemAmount,
        this.currentForm.underlyingDecimals
      );
      this.onChangeInpR();
    },
    closeDialog() {
      this.supplyAmount = 0;
      this.redeemAmount = 0;
      this.$emit("closeDialogVisible");
    },
    async flushStatus() {
    },
    onChangeInp() {
      let e = this.supplyAmount;
      /* eslint-disable */
      e = e.replace(/[^\d.]/g, ""); // """."
      e = e.replace(/^0(\d+)/g, "$1"); // 0
      e = e.replace(/^\./g, ""); // 
      e = e.replace(/\.{2,}/g, "."); // . 
      e = e.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
      // e = e.replace(/^(\-)*(\d+)\.(\d{6}).*$/, "$1$2.$3"); // 6
      e = e.replace(/^(\-)*(\d+)\.(\d{18}).*$/, "$1$2.$3"); // 18
      this.supplyAmount = e;
      const stakeAmount = this.$toToken(
        this.supplyAmount,
        this.currentForm.cTokenDecimals
      );
      this.getBorrowLimit(this.currentForm.cToken, stakeAmount, "mint");
    },
    onChangeInpR() {
      let e = this.redeemAmount;
      /* eslint-disable */
      e = e.replace(/[^\d.]/g, ""); // """."
      e = e.replace(/^0(\d+)/g, "$1"); // 0
      e = e.replace(/^\./g, ""); // 
      e = e.replace(/\.{2,}/g, "."); // . 
      e = e.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
      // e = e.replace(/^(\-)*(\d+)\.(\d{6}).*$/, "$1$2.$3"); // 6
      e = e.replace(/^(\-)*(\d+)\.(\d{18}).*$/, "$1$2.$3"); // 18
      this.redeemAmount = e;
      const stakeAmount = this.$toToken(
        this.redeemAmount,
        this.currentForm.cTokenDecimals
      );
      this.getBorrowLimit(this.currentForm.cToken, stakeAmount, "redeem");
    },
    async getBorrowLimit(cToken, amount, typ) {
      let info = this.globalInfo;
      let borrowLimit = await info.borrowLimit(cToken, amount, typ);
      console.log("new:", borrowLimit, amount, typ);
      let hopeBorrowed = borrowLimit[0];
      let hopeTotalLimit = borrowLimit[1];
      let hopeLimitPercent = borrowLimit[2];
      this.hopeBorrowed = hopeBorrowed;
      this.hopeTotalLimit = hopeTotalLimit;
      this.hopeLimitPercent = hopeLimitPercent;
    },
  }
};
</script>

