<template>
  <section>
    <!-- <h4>Borrow Markets</h4> -->
    <el-dialog
      :title="currentForm.asset"
      visible
      :width="screenWidth > defaultWidth ? defaultWidth + 'px' : '96%'"
      :close-on-click-modal="false"
      center
      @close="closeDialog"
    >
      <div slot="title" class="dialog-title">
        <!-- <i class="el-icon-lollipop"></i> -->
        <span class="title-text">{{ currentForm.name }}</span>
      </div>
      <el-tabs type="border-card">
        <el-tab-pane label="Borrow">
          <div class="formmain">
            <div class="input calculation">
              <el-input
                v-model="borrowAmount"
                placeholder="0"
                :controls="false"
                @input="onChangeInp"
              ></el-input>
              <el-button
                class="max-btn"
                type="info"
                size="mini"
                @click="max80fn"
              >
                80% LIMIT
              </el-button>
            </div>
            <div class="info">
              <div class="form">
                <!-- <a class="label-link"> -->
                <label class="dark label-link">Borrow Rates</label>
                <!-- </a> -->
                <div class="calculation bottom-line">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Borrow APY</span></span
                  >
                  <span>
                    {{ currentForm.borrowAPY | toPercent(100) }}{{ "%" }}
                  </span>
                </div>
                <div class="calculation">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Distribution APY</span>
                  </span>
                  <span>
                    <!-- APY210602 -->
                    {{ "-- " }}
                  </span>
                </div>
              </div>
              <div class="form">
                <label class="dark label-link">Borrow Limit</label>
                <div class="calculation bottom-line">
                  <span class="description">Borrow Balance</span>
                  <div class="row">
                    <span>
                      ${{ borrowed | fromToken(USDT_DECIMAL_PLACES) }}
                    </span>
                    <span v-if="borrowAmount > 0">
                      {{ " -> " }}${{
                        hopeBorrowed | fromToken(USDT_DECIMAL_PLACES)
                      }}
                    </span>
                  </div>
                </div>
                <div class="calculation">
                  <span class="description">Borrow Limit Used</span>
                  <div class="row">
                    <span>
                      {{ borrowedLimitPercent | toPercent(100) }}{{ "%" }}
                    </span>
                    <span v-if="borrowAmount > 0">
                      {{ " -> " }}{{ borrowAmountPercent | toPercent(1)
                      }}{{ "%" }}
                    </span>
                  </div>
                </div>
              </div>
              <el-progress
                :percentage="borrowAmountPercent"
                :show-text="false"
              ></el-progress>
              <div class="action-button">
                <el-button
                  v-if="borrowedLimitPercent >= 1 || hopeLimitPercent >= 1"
                  type="info"
                  style="width: 100%"
                  disabled
                >
                  INSUFFICIENT COLLATERAL
                </el-button>
                <el-button
                  v-else
                  type="primary"
                  style="width: 100%"
                  @click="borrow"
                  :loading="loading"
                >
                  BORROW
                </el-button>
              </div>
              <div class="bottom-note">
                <div class="calculation">
                  <span class="description">Currently Borrowing </span>
                  <span>
                    {{
                      currentForm.balance.borrowBalanceCurrent
                        | fromToken(currentForm.underlyingDecimals, 2)
                    }}{{ " " + currentForm.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="Repay">
          <div class="formmain">
            <div
              v-if="currentForm.balance.tokenAllowance > 0"
              class="input calculation"
            >
              <el-input
                v-model="repayAmount"
                placeholder="0"
                :controls="false"
                @input="onChangeInpR"
              ></el-input>
              <el-button
                class="max-btn"
                type="info"
                size="mini"
                @click="repayMaxfn"
              >
                MAX
              </el-button>
            </div>
            <div v-else>
              <img v-if="false" class="asset-logo" src="../../assets/logo.png" alt="" />
              <p class="small text-center">
                To Supply or Repay {{ currentForm.name }} to the Coco Lending, you need to enable it first.
              </p>
            </div>
            <div class="info">
              <div class="form">
                <!-- <a class="label-link"> -->
                <label class="dark label-link">Borrow Rates</label>
                <!-- </a> -->
                <div class="calculation bottom-line">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Borrow APY</span>
                  </span>
                  <span>
                    {{ currentForm.borrowAPY | toPercent(100) }}{{ "%" }}
                  </span>
                </div>
                <div class="calculation">
                  <span>
                    <i class="el-icon-lollipop"></i>
                    <span class="description">Distribution APY</span>
                  </span>
                  <span>
                    <!-- APY210602 -->
                    {{ "-- " }}
                  </span>
                </div>
              </div>
              <div class="form">
                <label class="dark label-link">Borrow Limit</label>
                <div class="calculation bottom-line">
                  <span class="description">Borrow Balance</span>
                  <div class="row">
                    <span>
                      ${{ borrowed | fromToken(USDT_DECIMAL_PLACES) }}
                    </span>
                    <span v-if="repayAmount > 0">
                      {{ " -> " }}${{
                        hopeBorrowed | fromToken(USDT_DECIMAL_PLACES)
                      }}
                    </span>
                  </div>
                </div>
                <div class="calculation">
                  <span class="description">Borrow Limit Used</span>
                  <div class="row">
                    <span>
                      {{ borrowedLimitPercent | toPercent(100) }}{{ "%" }}
                    </span>
                    <span v-if="repayAmount > 0">
                      {{ " -> " }}{{ repayAmountPercent | toPercent(1)
                      }}{{ "%" }}
                    </span>
                  </div>
                </div>
              </div>
              <el-progress
                :percentage="repayAmountPercent"
                :show-text="false"
              ></el-progress>
              <div class="action-button">
                <el-button
                  v-if="currentForm.balance.tokenAllowance > 0"
                  type="primary"
                  style="width: 100%"
                  :disabled="
                    $compare(
                      $toToken(repayAmount, currentForm.underlyingDecimals),
                      currentForm.balance.tokenBalance
                    ) > 0
                  "
                  @click="repayBorrow"
                  :loading="loading"
                >
                  REPAY
                </el-button>
                <el-button
                  v-else
                  type="primary"
                  style="width: 100%"
                  @click="enable"
                  :loading="loading"
                >
                  ENABLE
                </el-button>
              </div>
              <div class="bottom-note">
                <div class="calculation">
                  <span class="description">Wallet Balance</span>
                  <span>
                    {{
                      currentForm.balance.tokenBalance
                        | fromToken(currentForm.underlyingDecimals, 2)
                    }}
                    {{ currentForm.name }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </section>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { USDT_DECIMAL_PLACES } from "@/config/constants";

export default {
  name: "DashboardBorrowDialog",
  props: {
    dialogVisible: Boolean,
    currentForm: Object,
  },
  data() {
    return {
      USDT_DECIMAL_PLACES: USDT_DECIMAL_PLACES,
      screenWidth: document.body.clientWidth,
      defaultWidth: 400,
      currentDialogVisible: false,
      repayAmount: 0,
      borrowAmount: 0,
      status: {
        enabled: false,
        ctokenBalance: 0,
        borrowBalance: 0,
        marketRate: 0,
      },
      cToken: null,
      hopeBorrowed: 0,
      hopeTotalLimit: 0,
      hopeLimitPercent: 0,
      loading: false,
      repayMax: false,
    };
  },
  computed: {
    ...mapGetters([
      "address",
      "contracts",
      "fullscreenLoading",
      "borrowed",
      "borrowedLimitPercent",
      "globalInfo",
    ]),
    borrowAmountPercent: function () {
      let percent = 0;
      if (this.borrowAmount > 0) {
        percent = this.$toPercent(this.hopeLimitPercent, 100) * 1;
      } else {
        percent = this.$toPercent(this.borrowedLimitPercent, 100);
      }
      if (percent < 0) {
        percent = 0;
      }
      if (percent > 100) {
        percent = 100;
      }
      return percent;
    },
    repayAmountPercent: function () {
      let percent = 0;
      if (this.repayAmount > 0) {
        percent = this.$toPercent(this.hopeLimitPercent, 100) * 1;
      } else {
        percent = this.$toPercent(this.borrowedLimitPercent, 100);
      }
      if (percent < 0) {
        percent = 0;
      }
      if (percent > 100) {
        percent = 100;
      }
      return percent;
    },
  },
  created() {},
  mounted() {
    this.cToken = this.contracts.find(
      (item) => item.cToken.cToken.address == this.currentForm.cToken
    ).cToken;
  },
  methods: {
    ...mapMutations(["setShowFullscreenLoading"]),
    async enable() {
      this.loading = true;
      const tx = await this.cToken.enableMarket();
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async borrow() {
      console.log("borrow");
      if (this.borrowAmount === "" || this.borrowAmount == 0) {
        this.$notify.error({
          title: "error",
          message: "empty!",
        });
        return false;
      }
      const stakeAmount = this.$toToken(
        this.borrowAmount,
        this.currentForm.cTokenDecimals
      );
      if (this.currentForm.totalCash.lt(stakeAmount)){
        this.$notify.error({
          title: "error",
          message: "insufficient borrowing cash!",
        });
        return false;
      }
      this.loading = true;
      const tx = await this.cToken.borrow(stakeAmount);
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async repayBorrow() {
      if (this.repayAmount === "" || this.repayAmount == 0) {
        this.$notify.error({
          title: "error",
          message: "empty",
        });
        return false;
      }
      let maxRepayAmount = this.currentForm.balance.tokenBalance;
      let stakeAmount = this.$toToken(
        this.repayAmount,
        this.currentForm.cTokenDecimals
      );
      if (this.$compare(stakeAmount, maxRepayAmount) > 0) {
        this.$notify.error({
          title: "error",
          message: "insufficient funds",
        });
        return false;
      }
      if (this.repayMax) {
        this.repayMax = false;
        const info = this.globalInfo;
        stakeAmount = await info.perentLimit(
          this.currentForm.cToken,
          "repay",
          -1
        );
      }
      this.loading = true;
      const tx = await this.cToken.repayBorrow(stakeAmount);
      await this.$store.dispatch("CommitTx", tx);
      this.loading = false;
    },
    async max80fn() {
      const info = this.globalInfo;
      const maxBorrowAmount = await info.perentLimit(
        this.currentForm.cToken,
        "borrow",
        0.8
      );
      this.borrowAmount = this.$formatUnits(
        maxBorrowAmount,
        this.currentForm.underlyingDecimals
      );
      this.onChangeInp();
    },
    async repayMaxfn() {
      const info = this.globalInfo;
      const maxRepayAmount = await info.perentLimit(
        this.currentForm.cToken,
        "repay"
      );
      this.repayAmount = this.$formatUnits(
        maxRepayAmount,
        this.currentForm.underlyingDecimals
      );
      this.onChangeInpR();
      this.repayMax = true;
      //this.repayAmount = "max"
    },
    closeDialog() {
      console.log("closeDialog");
      this.borrowAmount = 0;
      this.repayAmount = 0;
      this.$emit("closeDialogVisible");
    },
    resetStatus() {
      this.setShowFullscreenLoading(false);
      this.closeDialog();
    },
    onChangeInp() {
      let e = this.borrowAmount;
      /* eslint-disable */
      e = e.replace(/[^\d.]/g, ""); // """."
      e = e.replace(/^0(\d+)/g, "$1"); // 0
      e = e.replace(/^\./g, ""); // 
      e = e.replace(/\.{2,}/g, "."); // . 
      e = e.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
      // e = e.replace(/^(\-)*(\d+)\.(\d{6}).*$/, "$1$2.$3"); // 6
      e = e.replace(/^(\-)*(\d+)\.(\d{18}).*$/, "$1$2.$3"); // 18
      this.borrowAmount = e;
      this.getBorrowLimit(this.currentForm.cToken, this.borrowAmount, "borrow");
    },
    onChangeInpR() {
      this.repayMax = false;
      let e = this.repayAmount;
      /* eslint-disable */
      e = e.replace(/[^\d.]/g, ""); // """."
      e = e.replace(/^0(\d+)/g, "$1"); // 0
      e = e.replace(/^\./g, ""); // 
      e = e.replace(/\.{2,}/g, "."); // . 
      e = e.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
      // e = e.replace(/^(\-)*(\d+)\.(\d{6}).*$/, "$1$2.$3"); // 6
      e = e.replace(/^(\-)*(\d+)\.(\d{18}).*$/, "$1$2.$3"); // 18
      this.repayAmount = e;
      this.getBorrowLimit(this.currentForm.cToken, this.repayAmount, "repay");
    },
    async getBorrowLimit(cToken, amount, typ) {
      amount = this.$toToken(amount, this.currentForm.underlyingDecimals);
      let info = this.globalInfo;
      let borrowLimit = await info.borrowLimit(cToken, amount, typ);
      let hopeBorrowed = borrowLimit[0];
      let hopeTotalLimit = borrowLimit[1];
      let hopeLimitPercent = borrowLimit[2];
      this.hopeBorrowed = hopeBorrowed;
      this.hopeTotalLimit = hopeTotalLimit;
      this.hopeLimitPercent = hopeLimitPercent;
    },
  },
};
</script>

