<template>
  <div class="container public-container-main">
    <div class="address-box" :class="{ initclass: !address }">
      <p v-if="address">
        {{ address | formatAddress(3, 3) }}
      </p>
      <p v-else @click="login">Connect Wallet</p>
    </div>
    <div class="balance-box">
      <p>
        {{ balance || "-" }}
        <i class="brand"></i>
      </p>
    </div>
    <el-dropdown @command="netSwitch">
      <span class="el-dropdown-link">
        {{ curNet }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for="net in networks.filter((net) => net != curNet)"
          :key="net"
          :command="net"
          :disabled="net == 'HarmonyMain'"
          >{{ net }}</el-dropdown-item
        >
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "DashboardHeader",
  components: {},
  data() {
    return {
      curNet: "DevNet",
      networks: ["HarmonyMain", "HarmonyTest", "DevNet"],
    };
  },
  computed: {
    ...mapGetters(["address", "balance"]),
  },
  methods: {
    async login() {
      await this.$store.dispatch("login", true);
    },
    async netSwitch(net) {
      await this.$store.dispatch("switchNet", net);
      this.curNet = net;
    },
  },
};
</script>

