<template>
  <div class="container">
    <DashboardHeader />
    <DashboardHeroine />
    <DashboardProgress />
    <section class="dashboard-box-bg">
      <section class="dashboard-box">
        <el-row type="flex" justify="center" class="row-bg">
          <el-col :xs="22" :sm="22">
            <DashboardSupply />
          </el-col>
        </el-row>
        <el-row type="flex" justify="center" class="row-bg">
          <el-col :xs="22" :sm="22">
            <DashboardBorrow />
          </el-col>
        </el-row>
      </section>
    </section>
    <!-- <DashboardFooter /> -->
  </div>
</template>

<script>
import DashboardHeader from "@/components/DashboardComponent/DashboardHeader.vue";
import DashboardHeroine from "@/components/DashboardComponent/DashboardHeroine.vue";
import DashboardProgress from "@/components/DashboardComponent/DashboardProgress.vue";
import DashboardSupply from "@/components/DashboardComponent/DashboardSupply.vue";
import DashboardBorrow from "@/components/DashboardComponent/DashboardBorrow.vue";
import { mapGetters } from "vuex";
import { USDT_DECIMAL_PLACES } from "@/config/constants";

export default {
  name: "Header",
  components: {
    DashboardHeader,
    DashboardHeroine,
    DashboardProgress,
    DashboardSupply,
    DashboardBorrow,
    // DashboardFooter,
  },
  data() {
    return {
      USDT_DECIMAL_PLACES: USDT_DECIMAL_PLACES,
      timer: null,
    };
  },
  computed: {
    ...mapGetters(["chainID", "address", "markets", "contracts"]),
  },
  async mounted() {
    console.log("--- -- apps created -- ---");
    await this.$store.dispatch("login");
    if(!this.address)
      setTimeout(()=>this.$store.dispatch('login'),1000);
    const loopFn = async () => {
      let period = 2000;
      try {
        await this.$store.dispatch("pollTask");
        period = 6000;
      } catch (e) {
        console.log("error", e);
      }
      setTimeout(loopFn, period);
    };
    loopFn();
  },
  destroyed() {
    console.log("--- destroyed ---");
    clearInterval(this.timer);
  },
  methods: {},
};
</script>

